.video {
    &:before {
        background-image: url($image_dir + 'video-bg.png' );
    }
}

.video__content {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 550px;
}

.video__watch {
    font-size: 16px;
    color: $text-on-dark-bg;
    text-align: center;
    background: none;
    border: none;
    position: relative;
    display: block;
    margin: 3px auto;
    font-weight: 900;
    letter-spacing: .5px;
    cursor: pointer;

    &:hover {
        .video__play {
            background: rgba(256,256,256,.1);
        }

        .video__play,
        .triangle,
        .triangle:before,
        .triangle:after {
            border-color: lightgrey;
        }
    }
}

.video__play {
    border: 3px solid $white-color;
    width: 100px;
    height: 100px;
    display: block;
    border-radius: 50%;
    margin: 5px auto 34px auto;
    padding-top: 5px;
    background: none;
    transition: .2s ease-in-out;
    outline: none;
}

.video__title {
    text-transform: uppercase;
    margin: 0;
    font-size: 1em;
}

.triangle {
    position: relative;
    text-align: left;
    left: 35px;
    top: -2px;
    display: block;
}

.triangle:before,
.triangle:after {
    content: '';
    position: absolute;
}

.triangle,
.triangle:before,
.triangle:after {
    width: 23px;
    height: 23px;
    border-top-right-radius: 20%;
}

.triangle {
    border: 4px solid $white-color;
    border-left: none;
    border-bottom: none;
    transform: rotate(30deg) skewX(-30deg) scale(1, .866);
}

.triangle:before {
    transform: rotate(-135deg) skewX(-45deg) scale(1.414, .707) translate(0, -50%);
    border: 4px solid $white-color;
    border-left: none;
    border-bottom: none;
    margin-top: -4px;
    margin-left: 0px;
}

.triangle:after {
    transform: rotate(135deg) skewY(-45deg) scale(.707, 1.414) translate(50%);
    border: 4px solid $white-color;
    border-left: none;
    border-bottom: none;
    margin-top: -4px;
    margin-left: 0px;
}